.title {
  color: white;
  font-weight: bold;
  font-size: 50px;
  border-bottom: 7px solid #40b649;
  padding-bottom: 15px;
}

@media screen and (max-width: 1023px) {
  .title {
    font-size: 40px;
  }
}

@media screen and (max-width: 575px) {
  /* 모바일 */
  .title {
    font-size: 7vw;
  }
}
